.categories .content .title, .categories .content .title-sec {
  font-family: "Open Sans", sans-serif;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: -0.1px;
  text-align: center;
  color: white; }

.categories {
  overflow: hidden; }
  .categories .background-overlay {
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    z-index: -10;
    width: 100vw;
    margin-top: -30px; }
    @media screen and (min-width: 820px) {
      .categories .background-overlay {
        width: 800px; } }
  .categories .content {
    padding-bottom: 20px; }
    .categories .content .content-row {
      text-align: center;
      justify-content: center;
      margin-bottom: 24px; }
    .categories .content .title {
      font-size: 24px;
      font-weight: 800;
      color: #354F53; }
      @media screen and (min-width: 1440px) {
        .categories .content .title {
          font-size: 36px; } }
    .categories .content .title-sec {
