.faq-section {
  margin-top: 100px; }
  .faq-section .faq-section-bg {
    background-color: #f2faf9; }
    .faq-section .faq-section-bg .content {
      margin: auto auto;
      padding: 55px; }
      .faq-section .faq-section-bg .content .center-align-row {
        padding-top: 55px;
        margin: auto; }
      .faq-section .faq-section-bg .content .col-md-8 {
        margin: auto; }
      .faq-section .faq-section-bg .content .faq-wrapper {
        float: none; }
        @media screen and (min-width: 990px) {
          .faq-section .faq-section-bg .content .faq-wrapper {
            float: right; } }
        .faq-section .faq-section-bg .content .faq-wrapper .faq-icon {
          width: 100%; }
          @media screen and (min-width: 768px) {
            .faq-section .faq-section-bg .content .faq-wrapper .faq-icon {
              /* margin: auto auto;
            position: absolute;
            z-index: 20;
            width: 100%;
            height: 100%; */ } }
          .faq-section .faq-section-bg .content .faq-wrapper .faq-icon img {
            width: 150px; }
            @media screen and (min-width: 768px) {
              .faq-section .faq-section-bg .content .faq-wrapper .faq-icon img {
                width: 250px; } }
        @media screen and (min-width: 768px) {
          .faq-section .faq-section-bg .content .faq-wrapper .faq-profile {
            margin: auto auto;
            width: 400px;
            height: 400px;
            object-fit: contain;
            z-index: 10;
            margin-left: 100px; } }
        .faq-section .faq-section-bg .content .faq-wrapper .faq-profile img {
          height: 250px;
          width: 250px; }
          @media screen and (min-width: 768px) {
            .faq-section .faq-section-bg .content .faq-wrapper .faq-profile img {
              position: absolute;
              height: 400px;
              width: 400px;
              object-fit: contain; } }
      .faq-section .faq-section-bg .content .faq-desc-wrapper {
        width: 100%; }
        .faq-section .faq-section-bg .content .faq-desc-wrapper .title {
          font-size: 24px;
          font-weight: 800;
          color: #354F53;
          text-align: left; }
          .faq-section .faq-section-bg .content .faq-desc-wrapper .title .desc {
            font-size: 16px;
            color: #46737A;
            font-weight: normal;
            width: 100%; }
            @media screen and (min-width: 1440px) {
              .faq-section .faq-section-bg .content .faq-desc-wrapper .title .desc {
                width: 60%; } }
          .faq-section .faq-section-bg .content .faq-desc-wrapper .title .faq-btn {
            width: 150px;
            height: 72px;
            border-radius: 36px;
            margin-top: 22px; }
            @media screen and (min-width: 768px) {
              .faq-section .faq-section-bg .content .faq-desc-wrapper .title .faq-btn {
                width: 200px;
                height: 72px;
                border-radius: 36px;
                margin-top: 22px; } }
